import { ItemWrapper, ListWrapper, Text } from "./iconList.styles";

type IconItem = {
    icon: any;
    body: string;
    link?: string;
}

type IconListProps = {
    icons: IconItem[];
}

export const IconList = ({
    icons
}: IconListProps) => {
    return(
        <ListWrapper>
            {icons.map((icon: IconItem) => 
                <ItemWrapper>
                    {icon.icon}
                    <Text>{icon.body}</Text>
                    {icon.link && <a href={icon.link} target="_blank" rel="noreferrer">→</a>}
                </ItemWrapper>
            )}
        </ListWrapper>
    )
}