import styled from "styled-components";

export const CardWrapper = styled.div`
    width: 30%;
    display: flex;
    flex-direction: column;
    margin: 8px 8px;
    align-items: center;
    background-color: white;
    border-radius: 4px;
    transition: box-shadow 0.3s ease-in-out;
    :hover {
        box-shadow: 0px 0px 16px -1px rgba(0,0,0,0.67);
    }
`