import { HeaderWrapper } from "../../../pages.styles";
import { Card } from "./components";
import { ContentWrapper, Title, Wrapper } from "./expertise.styles"
import SchoolIcon from '@mui/icons-material/School';

const expertises = [
    {
        icon: <SchoolIcon/>,
        title: 'JavaScript',
    },  {
        icon: <SchoolIcon/>,
        title: 'TypeScript',
    },  {
        icon: <SchoolIcon/>,
        title: 'Python',
    },  {
        icon: <SchoolIcon/>,
        title: 'Java',
    },  {
        icon: <SchoolIcon/>,
        title: 'Golang',
    }, {
        icon: <SchoolIcon/>,
        title: 'C',
    }, {
        icon: <SchoolIcon/>,
        title: 'Node.js',
    },  {
        icon: <SchoolIcon/>,
        title: 'ReactJS',
    },  {
        icon: <SchoolIcon/>,
        title: 'Django',
    },  {
        icon: <SchoolIcon/>,
        title: 'HTML',
    },  {
        icon: <SchoolIcon/>,
        title: 'CSS',
    },  {
        icon: <SchoolIcon/>,
        title: 'AWS',
    },
]

export const Expertise = () => {
    return(
        <Wrapper>
            <HeaderWrapper>
                <Title>My Expertise</Title>
                <a href={process.env.PUBLIC_URL + "/resume.pdf"}><Title>Download CV</Title></a>
            </HeaderWrapper>
            <ContentWrapper>
                {expertises.map((expertise) => <Card icon={expertise.icon} title={expertise.title}/>)}
            </ContentWrapper>
        </Wrapper> 
    )
}